body {
  position: relative;
}

.docs-sidebar {
  background: #f8fafc;
  border-right: 1px solid #e2e8f0;
  min-height: calc(100vh - 60px);
  padding: 2rem 1rem;
}

.docs-sidebar .nav {
  position: sticky;
  top: 2rem;
}

.sidebar-heading {
  color: #475569;
  text-transform: uppercase;
  letter-spacing: .05em;
  margin-bottom: .75rem;
  font-size: .875rem;
  font-weight: 600;
}

.docs-sidebar .nav-link {
  color: #64748b;
  border-radius: .375rem;
  padding: .5rem .75rem;
  font-size: .9375rem;
  transition: all .2s;
}

.docs-sidebar .nav-link:hover {
  color: #3b82f6;
  background: #3b82f61a;
}

.docs-sidebar .nav-link.active {
  color: #3b82f6;
  background: #3b82f61a;
  font-weight: 500;
}

.docs-content {
  padding: 2rem;
}

.docs-content h1 {
  color: #1e293b;
  margin-bottom: 2rem;
  font-size: 2.25rem;
  font-weight: 700;
}

.docs-content h2 {
  color: #334155;
  border-bottom: 2px solid #e2e8f0;
  margin: 2.5rem 0 1.5rem;
  padding-bottom: .5rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.docs-content h3 {
  color: #475569;
  margin: 2rem 0 1rem;
  font-size: 1.25rem;
  font-weight: 600;
}

.docs-content p {
  color: #475569;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  line-height: 1.7;
}

.docs-content pre {
  color: #fff;
  background: #1e1e1e;
  border-radius: .75rem;
  margin: 1.5rem 0;
  padding: 1.25rem;
  font-size: .875rem;
  overflow-x: auto;
  box-shadow: 0 1px 3px #0000001a;
}

.docs-content pre code {
  color: #fff;
}

.docs-content code {
  font-family: Fira Code, monospace;
  font-size: .875rem;
  line-height: 1.7;
}

.docs-content .file-path {
  color: #3b82f6;
  background: #3b82f61a;
  border-radius: .375rem;
  padding: .25rem .5rem;
  font-family: Fira Code, monospace;
  font-size: .875rem;
}

.docs-content .note {
  background: #3b82f61a;
  border-left: 4px solid #3b82f6;
  border-radius: .5rem;
  margin: 1.5rem 0;
  padding: 1rem;
}

.step-nav {
  border-top: 1px solid #e2e8f0;
  justify-content: space-between;
  margin-top: 3rem;
  padding-top: 1.5rem;
  display: flex;
}

.step-nav a {
  color: #3b82f6;
  border-radius: .5rem;
  align-items: center;
  gap: .5rem;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.step-nav a:hover {
  background: #3b82f61a;
}

@media (width <= 768px) {
  .docs-sidebar {
    border-bottom: 1px solid #e2e8f0;
    border-right: none;
    min-height: auto;
  }

  .docs-sidebar .nav {
    position: static;
  }

  .docs-content {
    padding: 1.5rem;
  }
}
